<template>
  <div class="home-view">
    <section class="hero-section">
      <div class="container">
        <h1>Menemani Setiap Langkah Kehamilan Anda</h1>
        <p>Sumber informasi dan layanan terpercaya untuk calon ibu. Jelajahi artikel, konsultasi, dan layanan kami.</p>
        <router-link to="/articles">
          <AppButton variant="primary">Lihat Semua Artikel</AppButton>
        </router-link>
      </div>
    </section>

    <section class="featured-articles">
      <div class="container">
        <h2>Artikel Terbaru Untuk Anda</h2>
        <div v-if="loading" class="loading">Memuat artikel...</div>
        <div v-else class="article-grid">
          <ArticleCard 
            v-for="article in featuredArticles" 
            :key="article.id" 
            :article="article" 
          />
        </div>
      </div>
    </section>
    
    <section class="services-section">
        <div class="container">
            <h2>Layanan Kami</h2>
            <div class="service-cards">
                <div class="service-card">
                    <h3>Yoga Prenatal</h3>
                    <p>Tingkatkan fleksibilitas dan ketenangan Anda selama kehamilan dengan kelas yoga yang dirancang khusus.</p>
                    <router-link to="/reservation"><AppButton variant="secondary">Reservasi Sekarang</AppButton></router-link>
                </div>
                <div class="service-card">
                    <h3>Pijat Bayi</h3>
                    <p>Pelajari teknik pijat bayi untuk meningkatkan ikatan dan merangsang perkembangan si kecil. (Segera Hadir)</p>
                </div>
            </div>
        </div>
    </section>

  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import ApiService from '@/services/ApiService';
import AppButton from '@/components/common/AppButton.vue';
import ArticleCard from '@/components/articles/ArticleCard.vue';

const articles = ref([]);
const loading = ref(true);

const featuredArticles = computed(() => articles.value.slice(0, 3));

onMounted(async () => {
  try {
    const response = await ApiService.getArticles();
    articles.value = response.data;
  } catch (error) {
    console.error("Gagal mengambil artikel unggulan:", error);
  } finally {
    loading.value = false;
  }
});
</script>

